___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --main-color : #884A39;
  --second-color : #C38154;
  --heart-color :  #FFC26F;
}

img {
  display: block;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins';
}

textarea {
  resize: none;
}

.text-center {
  text-align: center;
  padding: 5px;
}

.p-2 {
  padding: 20px;
}

.pb-1 {
  padding-bottom: 10px;
}

.pt-3 {
  padding-top: 30px;
}
.mb-2 {
  margin-bottom: 20px;
}

a {
  text-decoration: none;
  color: var(--main-color);
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: var(--main-color);
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 60px;
}

header {
  margin-top: 44px;
}

nav {
  font-size: 1.5em;
  font-weight: 600;
  display: flex;
  position: fixed;
  top: 0;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background-color: var(--main-color);
  color: white;
  z-index: 100;
  width: 100%;
}

nav .nav-brand {
  margin-left: 10px;
}

.drawer {
  position: static;
}

#menu {
  display: none;
  height: 55px;
  width: 55px;
}

.nav-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-right: 22px;
}

.nav-items {
  list-style: none;
}

nav a {
  text-decoration: none;
  padding: 14px;
  color: white;
  margin-right: 6px;
  margin-left: 6px;
}

nav .nav-items a {
  position: relative;
  font-size: .7em;
}

nav .nav-items a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: white;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

nav .nav-items a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-size: cover;
  isolation: isolate;
  color: white;
  height: 380px;
  position: relative;

}

.hero .hero-inner {
  width: 100%;
  margin-top: 60px;
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.hero::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: black;
  opacity: .5;
}

.hero h1 {
  margin-bottom: 20px;
  font-size: 2.7em;
}

.hero p {
  font-size: 1.2em;
}

.hero #search-bar {
  max-width: 400px;
  margin: 40px auto;
  position: relative;
}

.hero #search-bar input {
  width: 100%;
  height: 40px;
  padding: 20px 15px 20px 40px;
  border-radius: 20px;
  font-size: 1em;
}

.hero span {
  position: absolute;
  left: 10px;
  top: 10px;
}

#error-page {
  margin: 40px 0;
}

button.refresh {
  display: block;
  margin: 20px auto;
  padding: 12px;
  background-color: var(--main-color);
  color: white;
  border: none;
  border-radius: 5px;
}

button.refresh:hover {
  cursor: pointer;
  background-color: var(--second-color);
}

#content {
  display: grid;
}

#content-detail {
  margin: auto;
  max-width: 800px;
  column-gap: 20px;
}

#content-detail p {
  font-size: .9em;
}

#content-detail .wrapper-information img {
  width: 100%;
}

.information {
  padding: 10px;
}

.information p {
  margin-bottom: 10px;
}

.menus {
  margin: 20px 0;
  border: 1px solid var(--main-color);
}

.menus h4{
  background-color: var(--main-color);
  color: white;
}

.menus p {
  margin: 0 15px;
  padding: 10px;
  border-bottom: 1px solid #eaeaea;
}

section {
  margin: 0 32px;
  display: grid;
  justify-content: center;
}

section h2 {
  margin: 22px 0;
  color: black;
  font-size: 1.5em;
}

article {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  position: relative;
}

.img-detail {
  width: 100%;
  height: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.img-description {
  width: 100%;
  height: 200px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  object-fit: cover;
}

article img {
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

article .description {
  margin: 16px 32px 32px;
}

article .city {
  position: absolute;
  top: 10px;
  padding: 5px 15px;
  background-color: var(--main-color);
  color: white;
  border-radius: 2px;
  border-bottom-right-radius: 15px;
}

article .description p {
  font-size: 14px;
}

article .description .rating {
  font-size: 16px;
  color: black;
}

article .description a {
  display: inline-block;
  font-weight: 600;
  font-size: 1.2em;
  padding: 12px 0;
  cursor: pointer;
}

article .description .text {
  position: relative;
  width: 100%;
  padding-bottom: 20px;
}

article .description .text p.hide {
  position: absolute;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

article .description .text button {
  font-size: 14px;
  background-color: transparent;
  border: none;
  color: #999;
  cursor: pointer;
}

#customer-reviews {
  display: grid;
  grid-template-columns: 20px 1fr;
  column-gap: 15px;
}

#customer-reviews img {
  width: 100%;
  border-radius: 50%;
}

#customer-reviews p:first-child {
  font-size: .8em;
  margin-bottom: 5px;
}

#customer-reviews p:last-child {
  color: #999;
  font-size: .7em;
}

.review-wrapper form{
  display: flex;
  flex-direction: column;
  width: 60%;
}

.review-wrapper form input, textarea {
  padding: 10px 5px;
}

.review-wrapper form button {
  background-color: var(--main-color);
  color: white;
  width: 100px;
  padding: 12px;
  border: none;
  border-radius: 4px;
}

.review-wrapper form button:hover {
  background-color: var(--second-color);
  cursor: pointer;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--heart-color);
  position: fixed;
  bottom: 75px;
  right: 50px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: white;
  cursor: pointer;
}

footer {
  background-color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 10px;
}

footer p {
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 16px 16px;
  text-align: center;
  color: #fff;
}

footer p a {
  text-decoration: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;EACtB,wBAAwB;EACxB,wBAAwB;AAC1B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;EACtB,sBAAsB;AACxB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,wBAAwB;AAC1B;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,YAAY;EACZ,mCAAmC;EACnC,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,aAAa;EACb,eAAe;EACf,MAAM;EACN,8BAA8B;EAC9B,mBAAmB;EACnB,YAAY;EACZ,mCAAmC;EACnC,YAAY;EACZ,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,aAAa;EACb,YAAY;EACZ,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,WAAW;EACX,WAAW;EACX,kBAAkB;EAClB,uBAAuB;EACvB,SAAS;EACT,OAAO;EACP,uBAAuB;EACvB,oBAAoB;EACpB,qCAAqC;AACvC;;AAEA;EACE,sBAAsB;EACtB,oBAAoB;AACtB;;AAEA;EACE,yDAAsE;EACtE,2BAA2B;EAC3B,sBAAsB;EACtB,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,kBAAkB;;AAEpB;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,kBAAkB;EAClB,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,MAAM;EACN,OAAO;EACP,WAAW;EACX,uBAAuB;EACvB,WAAW;AACb;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,4BAA4B;EAC5B,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,SAAS;AACX;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,cAAc;EACd,iBAAiB;EACjB,aAAa;EACb,mCAAmC;EACnC,YAAY;EACZ,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,qCAAqC;AACvC;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,mCAAmC;A